.formInput{
    display: flex;
    flex-direction: column;
    width: 100%;
    
    
}

input{
    padding: 15px;
    margin: 10px 0px;
    border-radius: 5px;
    border: 1px solid gray;
}


label{
    font-size: 12px;
    color: gray;
}

span{
    font-size: 12px;
    padding: 3px;
    color: red;
    display: none;
}

input:invalid[focused="true"]{
    border: 1px solid red;
}

input:invalid[focused="true"] ~ span{
    display: block;
}

.form-registration{
    width:50%;
    float: left;
}

.global-div{
    width: 50%;
    float: left;
    padding: 10%;
}

.global-div img{
    width: 100%;
    float: left;
}




.form-registration div:nth-child(2) input{
   
    background-image:url('../nameinputicon.png');
    background-repeat: no-repeat;
    background-position: right; 
   
  }

  .form-registration div:nth-child(3) input{
   
    background-image:url('../emailinputicon.png');
    background-repeat: no-repeat;
    background-position: right; 
   
  }

  
  .form-registration div:nth-child(4) input{
   
    background-image:url('../passwordinputicon.png');
    background-repeat: no-repeat;
    background-position: right; 
   
  }

  .form-registration div:nth-child(5) input{
   
    background-image:url('../repeatpasswordicon.png');
    background-repeat: no-repeat;
    background-position: right; 
      
  }



  @media screen and (max-width: 730px) {
    .global-div, .form-registration{
        width:100%;
        float:left;
        padding: 0;
        margin: 0;
    }
    .app{
        display: block;
        height: auto;
        height: auto;
    }

    #root{
        width: 100%;
    float: left;
    margin: 0;
    padding: 20px;
    }

  }

  